import Vue from 'vue'
import VueRouter from 'vue-router'
import AppSettingView from '@/views/AppSettingView'
import DashboardView from '@/views/DashboardView'
import ImportSettingView from '@/views/ImportSettingView'
import LineSettingView from '@/views/LineSettingView'
import TimeSettingView from '@/views/TimeSettingView'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Dashboard',
    component: DashboardView
  },
  {
    path: '/app',
    name: 'AppSetting',
    component: AppSettingView
  },
  {
    path: '/import',
    name: 'ImportSetting',
    component: ImportSettingView
  },
  {
    path: '/line',
    name: 'LineSetting',
    component: LineSettingView
  },
  {
    path: '/time',
    name: 'TimeSetting',
    component: TimeSettingView
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
